@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap);
body {
  margin: 0;
  font-family: libre baskerville, georgia, serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 101vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.submit-booklet {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    width:  100%;
    height:  100vh;
    margin: 0;
    padding: 24px;
    font-family: libre baskerville, georgia, serif;
  }
  
  .submit-booklet .book-list-header {
    color: black;
  }
  
  .author-name, .book-title, .book-content {
    width: 75%;
    height: 48px;
    font-size: 24px;
    padding-left: 12px;
    padding-right: 12px;
    margin: auto;
    margin-top: 12px;
    margin-bottom: 12px;
    border-radius: 4px;
    border: 1px solid black;
  }
  
  .author-name.has-error, .book-title.has-error, .book-content.has-error {
    border: 1px solid red;
  }
  
  ::-webkit-input-placeholder { 
    color: #aaa;
  }
  
  :-ms-input-placeholder { 
    color: #aaa;
  }
  
  ::placeholder { 
    color: #aaa;
  }
  
  .book-content  {
    height: 50%;
    font-size: 16px;
    padding-top: 12px;
    padding-right: 12px;
    padding: 12px 12px 0 12px;
    margin-top: 16px;
  }
  
  .submit-book {
    width: 120px;
    height: 48px;
    background: #4d94ff;
    font-size: 18px;
    color: #fff;
    border-radius: 4px;
    margin-top: 16px;  
    border:1px solid white;
    margin-left: calc( 87.5% - 120px + 12px );
  }
.editable-book .author-name, .editable-book .book-name, .editable-book .editable-book-content {
    width: calc(100% - 24px);
    height: 48px;
    font-size: 16px;
    padding-left: 12px;
    padding-right: 12px;
    margin: auto;
    margin-top: 12px;
    margin-bottom: 12px;
    border-radius: 4px;
    border: 1px solid black;
  }
  
  ::-webkit-input-placeholder { 
    color: #aaa;
  }
  
  :-ms-input-placeholder { 
    color: #aaa;
  }
  
  ::placeholder { 
    color: #aaa;
  }
  
  .editable-book .editable-book-content  {
    height: 50vh;
    font-size: 18px;
    padding-top: 12px;
    padding-right: 12px;
    padding: 12px 12px 0 12px;
    margin-top: 16px;
  }
  
  .editable-book  .button-wrapper {
    width: 75%;
    display: flex;
    justify-content: left;
  }
  
  .editable-book button {
    width: 120px;
    height: 48px;
    background: #4d94ff;
    font-size: 18px;
    color: #fff;
    border-radius: 4px;
    margin-top: 16px;  
    border:1px solid white;
  }
  
  .editable-book button:first-child {
    margin-right: 36px;
  }
.book-view {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    width: 75%;
    padding: 12px 0;
    text-align: left;
    max-width: 525px;
    margin: 0;
  }
  
  .editable.book {
    cursor: pointer;
  }
  
  .title-author-wrapper {
    display: flex;
    flex-direction: row;
    width: 75vw;
  }
  
  .paragraph-wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    color: #222222;
    -webkit-font-smoothing: antialiased;
  }
  
  .paragraph {
    /* text-indent: 2em; */
    margin: 0 0 10px 0;
    overflow-wrap: break-word;
    font-style: italic;
    font-size: 1.2em;
    line-height: 140%;
    font-family: libre baskerville, georgia, serif;
  }
  
  .author-title {
    font-size: 14px;
    font-family: georgia, georgia, serif;
    line-height: 180%;
    padding-left: calc((100% - 525px) / 2);
  }
  
  .author-title .title-label {
    font-style: italic;
  }
  
  .book-border  {
    margin-top: 80px;
    margin-bottom: 40px;
    width: 50%;
    margin-left: 25%;
    border-bottom: .5px solid rgba(100, 100, 100, .3);
  }
.book-list-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-color: #fff;
    width:  100%;
    padding: 24px;
    padding-bottom: 0;
    min-height: 100vh;
    box-sizing: border-box;
  }
  
  .book-list-header {
    color: #4d94ff;
    font-size: 11px;
    font-family: libre baskerville, georgia, serif;
    letter-spacing: 0.025em;
    margin-bottom: 24px;
  }
  
  .book-list-wrapper .book-list-header {
    text-decoration: underline;
  }
  
  .book-list-wrapper .book-list-header:hover {
    cursor: pointer;
  }
.App {
  text-align: center;
}
